import React, { useEffect, useState } from 'react';
import { WhiteSpace, Picker, InputItem, Steps, Toast } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import { useMutation } from '@apollo/client';
import { ejuSubject, artScience } from '@/constants/globalDatas';
import PickItem from '@/components/PickItem';
import { USER_COMPLETE } from '@/helpers/APIHelper';
import { Page, PageTitle, OthersItem, PrimaryButton } from '../styles';

const { Step } = Steps;
const Complete: React.FC = () => {
  const [artScienceValue, setArtScienceValue] = useState<string[]>([]); // 学科的值
  const [ejuSubjectValue, setEjuSubjectValue] = useState<string[]>([]); // 科目
  const [nickName, setNickName] = useState(''); // 科目
  const [addUserInfo, { data, error }] = useMutation(USER_COMPLETE, {
    onCompleted: () => history.push('/complete-all'),
  });
  const history = useHistory();
  // 设置下拉选中的值
  const handleSelectPick = (value: string[], type: string): void => {
    console.log(value);
    switch (type) {
      case 'artScience':
        setArtScienceValue(value);
        break;
      case 'ejuSubject':
        setEjuSubjectValue(value);
        break;
      default:
    }
  };

  // 校验值
  const checkValues = (): boolean => {
    switch (false) {
      case !!nickName:
        Toast.info('请输入昵称');
        break;
      case artScienceValue.length > 0:
        Toast.info('请选择学科');
        break;
      case ejuSubjectValue.length > 0:
        Toast.info('请选择科目');
        break;
      default:
        return true;
    }
    return false;
  };

  // 点击提交
  const handleSubmit = (): void => {
    if (checkValues()) {
      addUserInfo({
        variables: {
          nickName,
          artScience: artScienceValue[0],
          ejuSubject: ejuSubjectValue[0],
        },
      });
      // history.push('/complete-all');
    }
  };

  useEffect(() => {
    console.log(error && error.message);
  }, [error]);
  return (
    <Page>
      <PageTitle>请完善您的信息</PageTitle>
      <WhiteSpace size="xl" />
      <Steps direction="horizontal" current={0}>
        <Step />
        <Step />
        <Step />
      </Steps>
      <WhiteSpace size="xl" />
      <OthersItem>必填信息</OthersItem>
      <WhiteSpace size="xl" />
      <InputItem
        placeholder="请输入昵称"
        onChange={(val: string): void => setNickName(val)}
      />
      <WhiteSpace size="lg" />
      <Picker
        data={artScience}
        cols={1}
        value={artScienceValue}
        onOk={(value: string[]): void => handleSelectPick(value, 'artScience')}
      >
        <PickItem placeholder="请选择文理科" />
      </Picker>
      <Picker
        data={ejuSubject}
        cols={1}
        value={ejuSubjectValue}
        onOk={(value: string[]): void => handleSelectPick(value, 'ejuSubject')}
      >
        <PickItem placeholder="请选择考学科目" />
      </Picker>
      <WhiteSpace size="xl" />
      <PrimaryButton type="primary" onClick={handleSubmit}>
        下一步
      </PrimaryButton>
    </Page>
  );
};

export default Complete;
